import React, {useState} from 'react';
import {useNavigate } from "react-router-dom";
function Editsection({user, ...props}) {
    console.log(props)
    console.log(user)
    console.log(window.location.href)
    const navigate = useNavigate();
    const [firstName, setFirstName] = useState(user.firstName);
    const [email, setEmail] = useState(user.email);
    const [lastName, setLastName] = useState(user.lastName);
    const [avatar, setAvatar] = useState("");
    console.log(avatar)
    const onImageChange = (event) => {
        if (event.target.files && event.target.files[0]) {
          let reader = new FileReader();
          reader.onload = (e) => {
            setAvatar( e.target.result);
          };
          reader.readAsDataURL(event.target.files[0]);
        }
      }
    function cancel() {
        navigate('/profile')
    }
    console.log(firstName);
    return (
            <div className="profile__section" >           
                <img src={avatar || "/avatar.png"} className="profile__picture" alt="profile" />
                <input type="file" onChange={(e) => onImageChange(e) }/>
                <label htmlFor="firstName">First Name</label>
                <input type="text" name="firstName" maxLength="256" value={firstName} onChange={(e) => setFirstName(e.target.value) }/>
                <label htmlFor="lastName">Last Name</label>
                <input type="text" name="lastName" maxLength="256" value={lastName} onChange={(e) => setLastName(e.target.value) }/>
                <label htmlFor="email">Email</label>
                <input type="email" name="email" maxLength="256" value={email} onChange={(e) => setEmail(e.target.value) }/>
                <button className="logout__button" onClick={cancel} >Cancel</button>
                <button className="edit__button" >Save</button>
            </div> 
    )
}

export default Editsection
