import React, {useEffect, useState} from 'react';
import {Helmet} from 'react-helmet';
import {useNavigate } from "react-router-dom";
function Login(props) {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [errorP, setErrorP] = useState("");
    const navigate = useNavigate();
    useEffect(() => {
            fetch('/login', { method: 'GET', mode: 'cors',headers: {
                'Content-Type': 'application/json'
              },})
            .then(response => {
               return response.json()
            } ).then(data => {
                console.log(data)
                if(data.msg === "/profile") {
                    navigate("/profile");
                }
            })

        },[]);
    function login() {
        fetch('/login',{method:"POST",headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({email, password})
        }).then(r => r.json()).then(data => { 
            console.log(data)
            if(data.msg !== "success") {
                console.log(data.msg)
                setErrorP(data.msg);
            } else {
                navigate('/profile');
            }
        })
    }
    return (
        <div className="main">
            <Helmet>
                <link rel="stylesheet" href="/component_css/login.css" />
            </Helmet>
        <div className="login__form"  >
            <p style={{color:"white"}}>{errorP}</p>
            <div className="email_section">
                <img src="../useremail.png" alt="useremail" />
                <input type="email" maxLength="256" name="email" onChange={(e) => {setEmail(e.target.value)}} />
            </div>
            <div className="password_section">
                <img src="../userpassword.png" alt="useremail" />
                <input type="password" maxLength="256" name="password" onChange ={(e) => {setPassword(e.target.value)}} />
            </div>
            <div className="login__form__links">
                <a href="/register">Create New Account</a>
                <a href="/forgetpassword">Forgot Password?</a>
            </div>
            <button onClick={login} className="submit__class">Login</button>
        </div>

    </div>
    )
}

export default Login
