import React, {useState, useEffect} from 'react'
import {Helmet} from 'react-helmet';

function Signup() {
    const [firstName, setFirstName] = useState("");
    const [lastName, setLastName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [isDisable, setIsDisable] = useState(true);

    useEffect(()=> {
        function validate() {
            if(firstName.length > 0 && lastName.length >0 && email.indexOf("@") !== -1 && email.indexOf(".") !== -1 && password !== "" && password.length>=8 && password === confirmPassword  ) {
                 setIsDisable(false);
            } else {
                setIsDisable(true);
            }
        }
        validate();
    },[firstName,lastName,password,confirmPassword,email])
    function signUp() {
        console.log(firstName);
        fetch('/signup',{method:"POST",headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({firstName, lastName, email, password, confirmPassword})
        }).then(r => r.json()).then(data => { 
            alert(data.msg)
        })
    }
    return (
        <div className="main">
            <Helmet>
                <link rel="stylesheet" href="/component_css/signup.css" />
            </Helmet>
       <div className="register">
            <div className="register__header">
                <h2>Register</h2>
            </div>
            <div className="register__form">
                <input type="text" name="firstName" maxLength="256" placeholder=" First Name" onChange={(e) => {
                    setFirstName(e.target.value);
                    }} />
                <small style={{color:"red",display:!firstName?true:"none" }}>Field required</small>
                <input type="text" name="lastName" maxLength="256" placeholder=" Last Name" onChange={(e) => {
                    setLastName(e.target.value)
                    }} />
                <small style={{color:"red",display:!lastName?true:"none" }}>Field required</small>
                <input type="email" name="email" maxLength="256" placeholder=" Email" onChange={(e) => {
                    
                    setEmail(e.target.value)}}/>
                <small style={{color:"red",display:(email.indexOf("@")===-1 || email.indexOf(".")===-1) ?true:"none" }}>Invalid Email</small>
                <input type="password" name="password" maxLength="256" placeholder=" Password" onChange={(e) => {
                    setPassword(e.target.value)}}  />
                <small style={{color:"red",display:password.length<8?true:"none" }}>Password must contain 8 minimum character</small>
                <input type="password" name="ConfirmPassword" maxLength="256" placeholder=" Confirm Password" onChange={(e) => {
                    setConfirmPassword(e.target.value)}} />
                 <small style={{color:"red",display:confirmPassword !== password ?true:"none" }}>Password doesn't match</small>
                <button id="signUpButton" disabled={isDisable} style={{backgroundColor:isDisable?"red":null}} onClick={signUp}>Sign Up</button>
                <a href="/">Sign In</a>
            </div>
       </div> 
    </div>
    )
}

export default Signup
