import React,{useEffect, useState,} from 'react'
import {Helmet} from 'react-helmet';
import Editsection from './Editsection';
import {
    BrowserRouter as Router,
    Routes,
    Route,
    useNavigate,
  } from "react-router-dom";
function Profile(props) {
    // let { path, url } = useParams();
    // const inputEl = useRef(null);
    // console.log(url, path,inputEl);
    const navigate = useNavigate();
    const [showEditSection, setShowEditSection] = useState("none");
    const [showProfileSection, setShowProfileSection] = useState(true);
    const [user, setUser] = useState({email:"asdasd@mail.com", firstName: "Harut", lastName: "Badalyan" })
    useEffect(() => {
        console.log(showProfileSection)
        console.log(window.location.pathname);
        if (window.location.pathname === "/profile") {
            setShowProfileSection(true);
        }  
        if (window.location.pathname === "/profile/edit") {
            setShowProfileSection("none");
        } 
    },)
    function edit() {
        setShowEditSection(true);
        setShowProfileSection("none");
        navigate(`/profile/edit`);
    }
    function logOut() {
        fetch('/logout',{method:"POST",headers: {
            'Content-Type': 'application/json'
          },
        }).then(r => navigate("/"))
    }
    return (
        <div className="main">
            <Helmet>
                <link rel="stylesheet" href="/component_css/profile.css" />
            </Helmet>
            <Routes>
                <Route exact path={`/edit`} element={<Editsection   user={user} />} />
            </Routes>
            <div className="profile__section" style={{display:showProfileSection}}>           
                <img src="/avatar.png" className="profile__picture" alt="profile" />
                <div className="firstname__section">
                    <small>Firstname</small>
                    <p>firstname</p>
                </div>
                <div className="lastname__section">
                    <small>Lastname</small>
                    <p>lastname</p>
                </div>
                <div className="email__section">
                    <small>email</small>
                    <p>email</p>
                </div>
                <button className="logout__button" onClick={logOut} >Logout</button>
                <button className="edit__button" onClick={() => { edit()}} >Edit</button>
            </div> 
            <div className="posts__section">
                <textarea></textarea>
            </div>   
        </div>
    )
}

export default Profile
